import { API_URL, RES_PER_PAGE, KEY } from './config.js';
import { getJSON, sendJSON } from './helper.js';
import { AJAX } from './helper.js';

/**
 * ç±»åž‹ï¼Œç”¨æ¥å­˜æ”¾æ‰€æœ‰æ•°æ®
 * @typedef State
 * @property  {Recipe}  recipe  èœå•æ•°æ®
 * @property  {object}  search  æœç´¢ç›¸å…³çš„æ•°æ®
 * @property  {string}  search.query  è¾“å…¥çš„æœç´¢å­—ç¬¦ä¸²
 * @property  {object[]}  search.results  æœç´¢ç»“æžœ
 * @property  {number}  search.resultsPerPage æ¯é¡µå±•ç¤ºçš„æœç´¢ç»“æžœæ¡ç›®æ•°é‡
 * @property  {number}  search.page å½“å‰æ‰€åœ¨é¡µç å·
 * @property  {object[]}  bookmarks ä¹¦ç­¾æ•°æ®
 */

/**
 * åº”ç”¨çš„æ‰€æœ‰æ•°æ®
 * @type  State
 */
export const state = {
  recipe: {},
  search: {
    query: '',
    results: [],
    resultsPerPage: RES_PER_PAGE,
    page: 1,
  },
  bookmarks: [],
};

/**
 * ç±»åž‹ï¼Œèœå•æ•°æ®
 * @typedef Recipe
 * @property  {string}  id èœå•çš„id
 * @property  {string}  title èœå•æ ‡é¢˜
 * @property  {string}  publisher èœå•æ¥æº
 * @property  {string}  sourceUrl æ¥æºç½‘å€
 * @property  {string}  image å›¾ç‰‡ç½‘å€
 * @property  {number}  servings  ç”¨é¤äººæ•°
 * @property  {number}  cookingTime çƒ¹é¥ªæ—¶é—´
 * @property  {object}  ingredients çƒ¹é¥ªææ–™
 * @property  {boolean} bookmarked  æ˜¯å¦å·²åŠ å…¥ä¹¦ç­¾
 */

/**
 * åˆ›å»ºèœå•å¯¹è±¡
 * @param {object} data  ä¼ å…¥çš„èœå•æ•°æ®
 * @returns {Recipe}  è¿”å›žåˆ›å»ºå¥½çš„èœå•å¯¹è±¡
 */
const createRecipeObject = function (data) {
  const { recipe } = data.data;
  return {
    id: recipe.id,
    title: recipe.title,
    publisher: recipe.publisher,
    sourceUrl: recipe.source_url,
    image: recipe.image_url,
    servings: recipe.servings,
    cookingTime: recipe.cooking_time,
    ingredients: recipe.ingredients,

    // å°†é¢å¤–çš„å‚æ•°è‡ªåŠ¨å±•å¼€åŠ å…¥recipe objectä¸­
    ...(recipe.key && { key: recipe }),
  };
};

/**
 * åŠ è½½èœå•è¯¦æƒ…é¡µé¢
 * @param {string} id èœå•çš„id
 * @returns {Promise<Recipe>} è¿”å›žåŸºäºŽæ­¤èœå•idåˆ›å»ºçš„èœå•å¯¹è±¡
 * @throws å¦‚æžœæ— æ³•åŠ è½½ï¼Œå°±æŠ›å‡ºä¸€ä¸ªé”™è¯¯
 */
export const loadRecipe = async function (id) {
  try {
    const data = await AJAX(`${API_URL}${id}?key=${KEY}`);
    state.recipe = createRecipeObject(data);
    if (state.bookmarks.some(bookmark => bookmark.id === id))
      state.recipe.bookmarked = true;
    else state.recipe.bookmarked = false;
  } catch (err) {
    throw err;
  }
};

/**
 * åŠ è½½æœç´¢ç»“æžœ
 * @param {string} query è¾“å…¥çš„æœç´¢å­—ç¬¦ä¸²
 * @returns {Promise<Recipe>} è¿”å›žæœç´¢åˆ°çš„èœå•æ•°æ®
 * @throws å¦‚æžœæ— æ³•åŠ è½½ï¼Œå°±æŠ›å‡ºä¸€ä¸ªé”™è¯¯
 */
export const loadSearchResult = async function (query) {
  try {
    state.search.query = query;
    const data = await AJAX(`${API_URL}?search=${query}&key=${KEY}`);

    // å°†æœç´¢ç»“æžœåˆ›å»ºæ–° Recipe object
    state.search.results = data.data.recipes.map(rec => {
      return {
        id: rec.id,
        title: rec.title,
        publisher: rec.publisher,
        image: rec.image_url,
        ...(rec.key && { key: rec }),
      };
    });
    state.search.page = 1;
  } catch (err) {
    throw err;
  }
};

/**
 * è®¡ç®—æœç´¢ç»“æžœçš„é¡µæ•°
 * @param {number} page æ€»é¡µæ•°
 * @see loadSearchResult
 * @returns {Recipe} è¿”å›žæœç´¢åˆ°çš„èœå•æ•°æ®ï¼ˆéƒ¨åˆ†ï¼‰ä¸Ž loadSearchResulté‡Œè¿”å›žçš„æ•°æ®ç»“æž„ç›¸åŒ
 */
export const getSearchResultsPage = function (page = state.search.page) {
  state.search.page = page;
  const start = (page - 1) * state.search.resultsPerPage;
  const end = page * state.search.resultsPerPage;
  return state.search.results.slice(start, end);
};

/**
 * æ›´æ–°ç”¨é¤äººæ•°
 * @param {number} newServings æ›´æ–°åŽçš„ç”¨é¤äººæ•°
 */
export const updateServings = function (newServings) {
  state.recipe.ingredients.forEach(ing => {
    ing.quantity = (ing.quantity * newServings) / state.recipe.servings;
  });
  state.recipe.servings = newServings;
};

/** å­˜å‚¨ä¹¦ç­¾æ•°æ®åˆ° localStorage*/
const persistBookmarks = function () {
  localStorage.setItem('bookmarks', JSON.stringify(state.bookmarks));
};

/**
 * æ·»åŠ ä¹¦ç­¾
 * @param {Recipe} recipe å·²æ·»åŠ ä¹¦ç­¾çš„èœå•æ•°æ®
 */
export const addBookMark = function (recipe) {
  // æ·»åŠ ä¹¦ç­¾æ•°æ®
  state.bookmarks.push(recipe);

  // æ ‡è®°ä¸ºå·²æ·»åŠ ä¹¦ç­¾
  if (recipe.id === state.recipe.id) state.recipe.bookmarked = true;
  persistBookmarks();
};

/**
 * åˆ é™¤ä¹¦ç­¾
 * @param {string} id éœ€è¦åˆ é™¤çš„ä¹¦ç­¾çš„id
 */
export const deleteBookMark = function (id) {
  const index = state.bookmarks.findIndex(el => el.id === id);
  state.bookmarks.splice(index, 1);

  // ç§»é™¤å½“å‰æ ‡è®°çŠ¶æ€
  if (id === state.recipe.id) state.recipe.bookmarked = false;
  persistBookmarks();
};

/** åˆå§‹åŒ–ä¹¦ç­¾ */
const init = function () {
  const storage = localStorage.getItem('bookmarks');
  if (storage) state.bookmarks = JSON.parse(storage);
};

init();

/** for debuging */
const clearBookmarks = function () {
  localStorage.clear('bookmarks');
};
// clearBookmarks();

/**
 * ä¸Šä¼ èœå•
 * @param {Recipe} newRecipe ç”¨æˆ·ä¸Šä¼ çš„èœå•æ•°æ®
 * @returns {Promise} å¤„ç†å¥½çš„èœå•æ•°æ®
 * @throws å¦‚æžœåˆ›å»ºå¤±è´¥ï¼ŒæŠ›å‡ºä¸€ä¸ªé”™è¯¯
 */
export const uploadRecipe = async function (newRecipe) {
  try {
    const ingredients = Object.entries(newRecipe)
      .filter(entry => entry[0].startsWith('ingredient') && entry[1] !== '')
      .map(ing => {
        const ingArr = ing[1].split(',').map(el => el.trim());
        // const ingArr = ing[1].replaceAll(' ', '').split(',');
        if (ingArr.length !== 3)
          throw new Error('Wrong ingredien format! Please try again');
        const [quantity, unit, description] = ingArr;
        return { quantity: quantity ? +quantity : null, unit, description };
      });

    const recipe = {
      title: newRecipe.title,
      source_url: newRecipe.sourceUrl,
      image_url: newRecipe.image,
      publisher: newRecipe.publisher,
      cooking_time: +newRecipe.cookingTime,
      servings: +newRecipe.servings,
      ingredients,
    };
    const data = await AJAX(`${API_URL}?key=${KEY}`, recipe);
    state.recipe = createRecipeObject(data);
  } catch (err) {
    throw err;
  }
};
