import { TIMEOUT_SET } from './config';

/**
 * è¶…æ—¶æŠ¥é”™
 * @param {number} s è¶…è¿‡å¤šå°‘ç§’å°± rejectè¯·æ±‚
 * @returns {Promise<Error>} rejectå¹¶æŠ›å‡ºä¸€ä¸ªError
 */
const timeout = function (s) {
  return new Promise(function (_, reject) {
    setTimeout(function () {
      reject(new Error(`Request took too long! Timeout after ${s} second`));
    }, s * 1000);
  });
};

/**
 * ä¸Šä¼ æ•°æ®
 * @param {string} url APIç«¯å£é“¾æŽ¥
 * @param {object} uploadData ä¸Šä¼ çš„æ•°æ®
 * @returns {object} è¿”å›žä¸Šä¼ æ•°æ®
 * @throws {error} å¦‚æžœä¸Šä¼ å¤±è´¥ï¼Œå°±æŠ›å‡ºä¸€ä¸ªerroråˆ°moduleä¸­
 */
export const AJAX = async function (url, uploadData = undefined) {
  try {
    const fetchPro = uploadData
      ? fetch(url, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(uploadData),
        })
      : fetch(url);

    //è¶…æ—¶å¤„ç†,å› ä¸ºè¿™é‡Œçš„æ•°å­—ä¸å®¹æ˜“ç†è§£ï¼Œæ‰€ä»¥ä¸ºäº†åŽæœŸç»´æŠ¤åº”è¯¥åœ¨configä¸­è®¾ç½®
    const res = await Promise.race([fetchPro, timeout(TIMEOUT_SET)]);
    const data = await res.json();

    if (!res.ok) {
      throw new Error(`${data.message} (${res.status})`);
    }
    return data;
  } catch (err) {
    throw err;
  }
};

